import React, {Component} from "react";
import './Main.css'
import VerticalCarousel from "./VerticalCarousel";
import { config } from "react-spring";

let slides = [
    {
        key: 0,
        gameName: 'Test',
        content: '유신이바보',
        icon: "logo512.png",
        link: 'aa'  
    },
    {
        key: 1,
        gameName: 'Test1',
        content: '유신이d보',
        icon: "logo512.png",
        link: 'aa'  
    },
    {
        key: 2,
        gameName: 'Test2',
        content: '유신이d보',
        icon: "logo512.png",
        link: 'aa'  
    },
    {
        key: 3,
        gameName: 'Test3',
        content: '유신이d보',
        icon: "logo512.png",
        link: 'aa'  
    }

]  


export default class Main extends Component {
    constructor(props){
        super(props);
        this.state = { config : config.gentle}  
    }

    render()
    {
        return ( 
            <div id="main" className="main" >
                <VerticalCarousel 
                    slides={slides}
                    animationConfig={this.state.config}
                />
            </div>
        );
    }
  }